<template>
	<div class="viewer">
		<Crumb />
		<div class="container" @click="onClick"></div>
	</div>
</template>

<script>
const chChangeLoadingHint = new BroadcastChannel('change-loading-hint');

export default {
	name: 'Viewer',
	methods: {
		async update () {
			chChangeLoadingHint.postMessage({
				name: '',
				action: 'show'
			});

			var article = this.$route.query.f, timestamp = this.$route.query.t * 1, author = this.$route.query.a || 'LostAbaddon';
			if (!article) {
				this.$router.push({path: '/'});
				return;
			}
			timestamp = timestamp || 0;
			var [content, copyright] = await Promise.all([
				Granary.getArticle(article, timestamp),
				Granary.getArticle('copyright.md'),
			]);

			var html = '', hasContent = true;
			if (!content) {
				hasContent = false;
				html = '<div class="page_not_found"><div class="frame"></div><div class="title"></div></div>';
			}
			else if (!!copyright) {
				content = content + '\n\n\n' + copyright;
			}

			if (hasContent) {
				if (timestamp === 0) timestamp = Date.now();
				html = MarkUp.parse(content, {
					toc: true,
					glossary: true,
					resources: false,
					showtitle: true,
					showauthor: true,
					author,
					date: timestamp,
					classname: 'markup-content',
				});
				if (!html) {
					html = '<div class="page_not_found"><div class="frame"></div><div class="title">MarkUp </div></div>';
					hasContent = false;
				}
			}
			this.$el.querySelector('div.container').innerHTML = html;
			if (hasContent) {
				MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
			}

			chChangeLoadingHint.postMessage({
				action: 'hide'
			});
		},
		onClick (evt) {
			var ele = evt.target;
			if (ele.nodeName.toLowerCase() === 'a') {
				let path = ele.getAttribute('href');
				if (!!path) {
					if (path.indexOf('/page/') === 0) {
						path = path.replace('/page/', '/');
						path = {path};
						evt.preventDefault();
						this.$router.push(path);
						return;
					}
					else if (path.indexOf('/article/') === 0) {
						path = path.replace('/article/', '');
						path = encodeURIComponent(path);
						path = {path: '/view', query: {f: path}};
						evt.preventDefault();
						this.$router.push(path);
						return;
					}
					else if (path.indexOf('/category/') === 0) {
						path = path.replace('/article/', '').replace(/[\\\/]+/g, ',');
						path = {path: '/category', query: {c: path}};
						evt.preventDefault();
						this.$router.push(path);
						return;
					}
				}
			}
		}
	},
	mounted () {
		this.update();
	}
}
</script>